:root {
  --width: 900px;
  --height: 300px;
  --perDistance: 150px;
}
.BrowserMode {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
  .videoWrap {
    z-index: 0;
    display: flex;
    justify-content: center;
  }
  .video {
    width: var(--width);
    height: var(--height);
    overflow: hidden;
    border-radius: 15px;
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .wrapper {
    display: flex;
    gap: 4px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    position: absolute;
    width: var(--width);
    height: var(--height);
    border-radius: 15px;
    .topWrapper {
      display: flex;
      .bg-white {
        margin-top: -10px;
        width: var(--perDistance);
        height: var(--perDistance);
        background: transparent;
        border-radius: 15px;
        margin: 0px 0px 0px 0px;
      }
      .bg1 {
        background: white;
        animation: moveBg1 5s infinite;
        border-radius: 15px 0px 0px 15px;
      }
      .bg2 {
        background: white;
        animation: moveBg2 5s infinite;
      }
      .bg3 {
        background: white;
        animation: moveBg3 5s infinite;
      }
      .bg4 {
        background: white;
        animation: moveBg4 5s infinite;
      }
      .bg5 {
        background: white;
        animation: moveBg5 5s infinite;
      }
      .bg6 {
        background: white;
        animation: moveBg6 5s infinite;
      }
    }
    .bottomWrapper {
      display: flex;

      .bg-white {
        width: var(--perDistance);
        height: var(--perDistance);
        background: transparent;
        border-radius: 15px;
        margin: 0px 0px 0px 0px;
      }
      .bg7 {
        background: white;
        animation: moveBg7 5s infinite;
        border-radius: 15px 15px 0px 15px;
        transform-origin: 0 100%;
      }
      .bg8 {
        background: white;
        animation: moveBg8 5s infinite;
        transform-origin: 0 100%;
      }
      .bg9 {
        background: white;
        animation: moveBg9 5s infinite;
        transform-origin: 0 100%;
      }
      .bg10 {
        background: white;
        animation: moveBg10 5s infinite;
        transform-origin: 0 100%;
      }
      .bg11 {
        background: white;
        animation: moveBg11 5s infinite;
        transform-origin: 0 100%;
      }
      .bg12 {
        background: white;
        animation: moveBg12 5s infinite;
        transform-origin: 0 100%;
      }
    }
  }
  .wrapper2 {
    display: flex;
    gap: 4px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 20;
    position: absolute;
    width: var(--width);
    height: var(--height);
    border-radius: 15px;
    .topWrapper {
      display: flex;
      .bg-trans {
        width: var(--perDistance);
        height: var(--perDistance);
        border: 1px solid red;
      }
    }
    .bottomWrapper {
      display: flex;
      .bg-trans {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: var(--perDistance);
        height: var(--perDistance);
        border: 1px solid red;
      }
      .textCol {
        display: flex;
        flex-direction: column;
        text-align: left;

        #span1 {
          color: #a7a7a9;
          font-size: 20px;
          margin-left: 5px;
        }
        #span2 {
          font-size: 20px;
          color: black;
          margin-left: 5px;
        }
      }

      .quarter {
        width: 30px;
        height: 30px;
      }

      .top-left {
        border-top-left-radius: 100px;
        background: linear-gradient(to bottom, #bbc4c9, white);
        background-repeat: no-repeat;
        float: left;
        img {
          position: relative;
          top: 18px;
          left: 16px;
          width: 10px;
          height: 10px;
        }
      }
      .top-right {
        border-bottom-left-radius: 100px;
        background-color: blue;
      }
    }
  }
}

@keyframes moveBg1 {
  0% {
    height: 0;
  }
  20% {
    height: 0;
  }
  40% {
    height: var(--perDistance);
    border-radius: 15px 0px 15px 0px;
  }
  60% {
    height: var(--perDistance);
    border-radius: 15px 0px 15px 0px;
  }
  80% {
    height: 0;
  }
  100% {
    height: 0;
  }
}
